"use client";

import Link from 'next/link';
import { siteDetails } from '@/data/siteDetails';
import { navigationLinks } from '@/data/navigationLinks';
import { Twitter, Linkedin, Github, Mail, Phone, MapPin } from 'lucide-react';
import { CSSProperties, useState, useEffect } from 'react';
import { motion } from 'framer-motion';

export default function Footer() {
  const [isMediumScreen, setIsMediumScreen] = useState(false);
  
  // Use useEffect for responsive design
  useEffect(() => {
    const handleResize = () => {
      setIsMediumScreen(window.innerWidth >= 768);
    };
    
    // Set initial value
    handleResize();
    
    // Add event listener
    window.addEventListener('resize', handleResize);
    
    // Clean up
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Footer styles
  const footerStyle: CSSProperties = {
    backgroundColor: 'rgba(0, 0, 0, 0.97)',
    paddingTop: '6rem',
    paddingBottom: '2rem',
    borderTop: '1px solid rgba(255, 255, 255, 0.05)'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '80rem',
    marginLeft: 'auto',
    marginRight: 'auto',
    paddingLeft: '1.5rem',
    paddingRight: '1.5rem'
  };

  const gridContainerStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: isMediumScreen ? 'repeat(2, 1fr)' : 'repeat(1, 1fr)',
    gap: '3rem',
    marginBottom: '4rem',
    color: 'var(--foreground)'
  };

  // Adjust for larger screens if needed
  useEffect(() => {
    if (isMediumScreen && typeof document !== 'undefined') {
      const gridContainer = document.querySelector('div[data-grid-container]');
      if (gridContainer && window.innerWidth >= 1024) {
        (gridContainer as HTMLElement).style.gridTemplateColumns = 'repeat(4, 1fr)';
      }
    }
  }, [isMediumScreen]);

  const logoStyle: CSSProperties = {
    fontSize: '1.25rem',
    fontWeight: '300',
    letterSpacing: '0.1em',
    color: 'var(--primary)',
    textTransform: 'uppercase',
    marginBottom: '1.5rem',
    display: 'inline-block'
  };

  const paragraphStyle: CSSProperties = {
    color: 'var(--muted-100)',
    marginBottom: '1.5rem',
    fontSize: '0.95rem',
    lineHeight: '1.7',
    fontWeight: '300'
  };

  const socialContainerStyle: CSSProperties = {
    display: 'flex',
    gap: '1rem'
  };

  const socialLinkStyle: CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: '2.5rem',
    height: '2.5rem',
    color: 'var(--foreground)',
    border: '1px solid rgba(255, 255, 255, 0.1)',
    transition: 'all 0.3s'
  };

  const sectionHeadingStyle: CSSProperties = {
    fontSize: '1rem',
    fontWeight: '300',
    marginBottom: '1.5rem',
    letterSpacing: '0.1em',
    textTransform: 'uppercase'
  };

  const linkListStyle: CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    gap: '0.75rem'
  };

  const linkStyle: CSSProperties = {
    color: 'var(--muted-100)',
    transition: 'color 0.3s',
    fontSize: '0.95rem',
    fontWeight: '300'
  };

  const contactItemStyle: CSSProperties = {
    display: 'flex',
    alignItems: 'flex-start',
    marginBottom: '1rem'
  };

  const contactIconStyle: CSSProperties = {
    color: 'var(--primary)',
    width: '1.25rem',
    height: '1.25rem',
    marginRight: '0.75rem',
    marginTop: '0.125rem',
    flexShrink: 0
  };

  const contactTextStyle: CSSProperties = {
    color: 'var(--muted-100)',
    fontSize: '0.95rem',
    fontWeight: '300'
  };

  const inputStyle: CSSProperties = {
    width: '100%',
    padding: '0.75rem 1rem',
    backgroundColor: 'transparent',
    border: '1px solid rgba(255, 255, 255, 0.1)',
    color: 'var(--foreground)',
    fontSize: '0.95rem',
    marginBottom: '1rem',
    fontWeight: '300'
  };

  const buttonStyle: CSSProperties = {
    width: '100%',
    padding: '0.75rem 2rem',
    backgroundColor: 'transparent',
    color: 'var(--primary)',
    border: '1px solid var(--primary)',
    fontWeight: '300',
    display: 'inline-block',
    textAlign: 'center',
    transition: 'all 0.3s',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.15em',
    textTransform: 'uppercase',
    cursor: 'pointer'
  };

  const hrStyle: CSSProperties = {
    borderTop: '1px solid rgba(255, 255, 255, 0.05)',
    margin: '2rem 0'
  };

  const bottomContainerStyle: CSSProperties = {
    display: 'flex',
    flexDirection: isMediumScreen ? 'row' : 'column',
    alignItems: 'center',
    justifyContent: 'space-between',
    gap: '1rem'
  };

  const copyrightStyle: CSSProperties = {
    color: 'var(--muted-100)',
    fontSize: '0.85rem',
    fontWeight: '300'
  };

  const bottomLinksStyle: CSSProperties = {
    display: 'flex',
    gap: '1.5rem'
  };

  const bottomLinkStyle: CSSProperties = {
    color: 'var(--muted-100)',
    fontSize: '0.85rem',
    fontWeight: '300',
    transition: 'color 0.3s'
  };

  return (
    <footer style={footerStyle}>
      <div style={containerStyle}>
        <div style={gridContainerStyle} data-grid-container>
          <div>
            <Link href="/" style={logoStyle}>
              {siteDetails.name}
            </Link>
            <p style={paragraphStyle}>
              Innovative technology solutions for businesses of all sizes. We help you transform and grow through cutting-edge technology.
            </p>
            <div style={socialContainerStyle}>
              <a
                href={siteDetails.socialLinks.twitter}
                target="_blank"
                rel="noopener noreferrer"
                style={socialLinkStyle}
                onMouseEnter={(e) => {
                  e.currentTarget.style.borderColor = 'var(--primary)';
                  e.currentTarget.style.color = 'var(--primary)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.1)';
                  e.currentTarget.style.color = 'var(--foreground)';
                }}
                aria-label="Twitter"
              >
                <Twitter size={18} />
              </a>
              <a
                href={siteDetails.socialLinks.linkedin}
                target="_blank"
                rel="noopener noreferrer"
                style={socialLinkStyle}
                onMouseEnter={(e) => {
                  e.currentTarget.style.borderColor = 'var(--primary)';
                  e.currentTarget.style.color = 'var(--primary)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.1)';
                  e.currentTarget.style.color = 'var(--foreground)';
                }}
                aria-label="LinkedIn"
              >
                <Linkedin size={18} />
              </a>
              <a
                href={siteDetails.socialLinks.github}
                target="_blank"
                rel="noopener noreferrer"
                style={socialLinkStyle}
                onMouseEnter={(e) => {
                  e.currentTarget.style.borderColor = 'var(--primary)';
                  e.currentTarget.style.color = 'var(--primary)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.1)';
                  e.currentTarget.style.color = 'var(--foreground)';
                }}
                aria-label="GitHub"
              >
                <Github size={18} />
              </a>
            </div>
          </div>
          
          <div>
            <h3 style={sectionHeadingStyle}>Quick Links</h3>
            <ul style={linkListStyle}>
              {navigationLinks.map((link) => (
                <li key={link.path}>
                  <Link 
                    href={link.path} 
                    style={linkStyle}
                    onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
                    onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
                  >
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
          
          <div>
            <h3 style={sectionHeadingStyle}>Contact Us</h3>
            <ul>
              <li style={contactItemStyle}>
                <Mail style={contactIconStyle} />
                <a 
                  href={`mailto:${siteDetails.contactEmail}`}
                  style={contactTextStyle}
                  onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
                  onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
                >
                  {siteDetails.contactEmail}
                </a>
              </li>
              <li style={contactItemStyle}>
                <Phone style={contactIconStyle} />
                <a 
                  href="tel:+15551234567"
                  style={contactTextStyle}
                  onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
                  onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
                >
                  +1 (555) 123-4567
                </a>
              </li>
              <li style={contactItemStyle}>
                <MapPin style={contactIconStyle} />
                <span style={contactTextStyle}>
                  {siteDetails.address.street}, {siteDetails.address.city}, {siteDetails.address.state} {siteDetails.address.zip}
                </span>
              </li>
            </ul>
          </div>
          
          <div>
            <h3 style={sectionHeadingStyle}>Newsletter</h3>
            <p style={paragraphStyle}>
              Subscribe to our newsletter for the latest updates and insights.
            </p>
            <form>
              <input
                type="email"
                placeholder="Your email address"
                style={inputStyle}
                onFocus={(e) => e.currentTarget.style.borderColor = 'var(--primary)'}
                onBlur={(e) => e.currentTarget.style.borderColor = 'rgba(255, 255, 255, 0.1)'}
                required
              />
              <motion.button
                type="submit"
                style={buttonStyle}
                whileHover={{ 
                  backgroundColor: 'rgba(255, 255, 255, 0.05)',
                  transform: 'translateY(-3px)'
                }}
                whileTap={{ 
                  transform: 'translateY(0)'
                }}
              >
                Subscribe
              </motion.button>
            </form>
          </div>
        </div>
        
        <hr style={hrStyle} />
        
        <div style={bottomContainerStyle}>
          <p style={copyrightStyle}>
            {siteDetails.copyright}
          </p>
          <div style={bottomLinksStyle}>
            <Link 
              href="/privacy" 
              style={bottomLinkStyle}
              onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
              onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
            >
              Privacy Policy
            </Link>
            <Link 
              href="/terms" 
              style={bottomLinkStyle}
              onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
              onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
            >
              Terms of Service
            </Link>
            <Link 
              href="/cookies" 
              style={bottomLinkStyle}
              onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
              onMouseLeave={(e) => e.currentTarget.style.color = 'var(--muted-100)'}
            >
              Cookie Policy
            </Link>
          </div>
        </div>
      </div>
    </footer>
  );
} 